import React, { useState } from 'react'
import { createContext } from "react";


export const useDemoContext=createContext();


export const DemoContext=({children})=> {

    const [data,setData]=useState([]);

const fetchData =async()=>{
    const response=await fetch("https://jsonplaceholder.typicode.com/todos")
    const data=await response.json()
    // console.log(data)
    setData(data)
}

const [dataByOne,setDataByOne]=useState({});
const fetchDataById =async(id)=>{
  // console.log(id)/
  const response=await fetch(`https://jsonplaceholder.typicode.com/todos/${id}`)
  const data=await response.json()
  // console.log(data)
  setDataByOne(data)
  // setData(data)
}
const [formData,setFormData]=useState({})
const submitData=(data)=>{
// console.log(data)
setFormData(data)
}

  return (
<useDemoContext.Provider value={{fetchData, data, fetchDataById,dataByOne,submitData ,formData}}>
     {children}

</useDemoContext.Provider>
  )
}
