import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Card, CardHeader,Box, CardBody, CardFooter, Heading, Button, Text } from '@chakra-ui/react'
import { Grid, GridItem } from '@chakra-ui/react'
import { clearCard } from './CardRedux'

export default function CardList() {

    const items = useSelector(state => state.card.cards);
    
    console.log(items)

const dispatch=useDispatch();

    return (
        <Box w='100%'  p='20px'>
             <Grid>
                <GridItem>
                    <Button onClick={()=>dispatch(clearCard())}>Clear</Button>
                </GridItem>
            </Grid>

            <Grid templateColumns='repeat(5, 1fr)' gap={6} w='100%' p='20px'>
                {items?.map((item, i) => {
                    console.log(item)
                    return (
                        <GridItem key={item.id} w='100%' h='10' bg='blue.500' >
                            <Card>
                                <CardHeader>
                                    <Heading size='md'> Item{i + 1}</Heading>
                                </CardHeader>
                                <CardBody>
                                    <Text>{item.item.item1}</Text>
                                    <Text>{item.item.item2}</Text>
                                    <Text>{item.item.item3}</Text>
                                </CardBody>
                                <CardFooter>
                                    <Button>Remove</Button>
                                </CardFooter>
                            </Card>

                        </GridItem>
                    )
                })}



            </Grid>
           
        </Box>
    )
}
