import { Box, Flex, Input, Button } from '@chakra-ui/react'
import React, { useState } from 'react'
import { FormControl,} from '@chakra-ui/react';
import { useDispatch, useSelector } from 'react-redux';
import { addCard } from './CardRedux';
import CardList from './CardList';

export default function Card() {



const dispatch =useDispatch();

    const [item, setItem] = useState({
        item1: "", item2: "", item3: ""
    })
    const handleChange = (e) => {
        const { name, value } = e.target;
        setItem({ ...item, [name]: value });
    }
    const handleSubmit = (e) => {
        e.preventDefault()
        console.log(item)
        dispatch(addCard({ id: Date.now(), item }));
        // setItem()

    }
  
    return (
        <Flex w='100%' justifyContent={'center'} alignItems={'center'} mt='30px' direction={'column'} gap='20px'>

            <form onSubmit={handleSubmit}>
                <FormControl m='2'>
                    <Input
                        type="text"
                        name='item1'
                        value={item.item1}
                        onChange={handleChange}
                    />
                </FormControl>
                <FormControl  m='2'>
                    <Input
                        type="text"
                        name='item2'
                        value={item.item2}
                        onChange={handleChange}
                    />
                </FormControl>
                <FormControl  m='2'>
                    <Input
                        type="text"
                        name='item3'
                        value={item.item3}
                        onChange={handleChange}
                    />  </FormControl>
                <Button m='5px' type="submit">Add card</Button>
            </form>

<CardList />
        </Flex>
    )
}
