// InDeRedux.js

import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  count:0,
};

const InDeRedux = createSlice({
  name: 'counter',
  initialState,
  reducers: {
    inCrement: (state) => {
    //   state.count +=1;
    console.log(state.count)
      state.count =state.count+1;

    },
    deCrement: (state) => {
        //   state.count +=1;
          state.count =state.count-1;
    
        },
  },
});

export const { inCrement, deCrement } = InDeRedux.actions;

export default InDeRedux.reducer;
