// store.js

import { configureStore } from '@reduxjs/toolkit';
import itemsReducer from './Redux.demo';
import itemsSliceDemo from './ReduxDemo';
import InDeRedux from './InDeRedux';
import CardRedux from './CardRedux';


const store = configureStore({
  reducer: {
    items: itemsReducer,
    items:itemsSliceDemo,
    counter:InDeRedux,
    card:CardRedux
  },
});

export default store;
