import React from 'react';
import Logo from './logo.svg';
import Props from './Props';

export default function First() {
    
    const style={
         style1:{
            color:"red",
        marginLeft:"40px"
        },
         style2:{
            color:"green",
        marginLeft:"40px"
        },
         style3:{
            color:"blue",
        marginLeft:"40px"
        }
    }
console.log(style.style1)
const handlechange=(e)=>{
  console.log(e.target)
}

  return (
  <React.Fragment>
  <div style={{marginLeft: '60px', marginTop:'30px', fontSize:"30px"}}>
    <Props img={Logo} name='React' styleData={style} hchange={handlechange} />
    {/* <img  src={Logo} alt='' width='50%' height='50%' /> */}
   </div>
 </ React.Fragment>
   
  )
}









