import { useState } from "react";

const initialData = {
    list: []
}

const TodoReducer = (state = initialData, action) => {

    switch (action.type){
        case "ADD_TODO":
            
            const { id, data } = action.payload;

            return{
                ...state,
                list: [
                    ...state.list,
                    {
                        id: id,
                        data: data
                    }
                ]
            }



            case "DELETETODO":
                const newList=state.list.filter((elem => elem.id !== action.id))
                return{
                    ...state,
                    list: newList
                }



                case "REMOVETODO":

                    return{
                        ...state,
                        list: []
                    }
                    
                    default: return state;
    }



}
export default TodoReducer;